.toolbar {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.search-wrapper {
  display: flex;
}

.search-input {
  height: 30px;
  font-size: 1rem;
  border: 2px solid #bdbdbd;
  border-right: 0;
  border-radius: 5px 0 0 5px;
}

.btn-search {
  width: 100px;
  font-size: 0.75rem;
  border: 2px solid #bdbdbd;
  border-radius: 0 5px 5px 0;
}

.git-hub-link {
  width: 100px;
  border: 1px solid #bdbdbd;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.75rem;
  text-align: center;
  line-height: 2;
}

.git-hub-link:hover {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 3px 3px;
}
